import Vue from 'vue'
import Router from 'vue-router'
import Menu from '@/components/Menu'
import Contact from '@/components/Contact'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Menu',
      component: Menu
    },
    {
    	path: '/contact',
    	name: 'Contact',
    	component: Contact
    }
  ]
})



